import React from 'react';
import {Figure} from 'react-bootstrap';

function Profile(){
    return(
        <>
            <Figure>
                <Figure.Image
                    width={70}
                    height={70}
                    alt="171x180"
                    src="https://png.pngtree.com/svg/20161113/ef1b24279e.png"
                />
                <Figure.Caption className="text-light">
                    Alan Saputra
                </Figure.Caption>
            </Figure>
        </>
    )
}
export default Profile;
